<template>
  <div class="row" v-if="isReady">
    <div class="col-12 mt-3 flex-x-between">
      <div class="flex-align-center">
        <h3 class="my-0">COdes</h3>
      </div>

      <div class="flex-align-center flex-end">
        <el-button
          class="mr-3 my-0"
          @click="printPdf"
          type="primary"
          v-ripple.400
        >
          Print
        </el-button>

        <el-button
          class="mx-0 my-0"
          @click="openFulfillDialog = true"
          type="primary"
          v-ripple.400
        >
          Add code
        </el-button>
      </div>
    </div>
    <div class="col-12 list-table mt-3">
      <div class="box pt-3">
        <div class="row">
          <div class="col-12 flex-align-center">
            <p
              class="mx-0 table-tab"
              :class="filters.status === '' && 'active-table-tab'"
              @click="filters.status = ''"
            >
              All
            </p>
            <p
              class="mx-0 table-tab"
              :class="filters.status === 'Received' && 'active-table-tab'"
              @click="filters.status = 'Received'"
            >
              Received
            </p>
            <p
              class="mx-0 table-tab"
              :class="filters.status === 'Pending' && 'active-table-tab'"
              @click="filters.status = 'Pending'"
            >
              Pending
            </p>
            <p
              class="mx-0 table-tab"
              :class="filters.status === 'Cancelled' && 'active-table-tab'"
              @click="filters.status = 'Cancelled'"
            >
              Cancelled
            </p>
          </div>
          <div class="col-12"><div class="b-divider mb-1 mt-0"></div></div>
          <div
            class="
              col-12
              mb-1
              d-flex
              justify-content-between
              align-items-center
            "
          >
            <div class="flex-align-center">
              <el-select
                v-model="filters.pageSize"
                class="mr-2 page-size-selector mr-2"
              >
                <el-option
                  v-for="page in 30"
                  :key="page"
                  :label="page * 5"
                  :value="page * 5"
                >
                </el-option>
              </el-select>
              <el-input
                v-model="filters.query"
                class="mr-2 table-search"
                placeholder="Type something"
                prefix-icon="bx bx-search"
              >
              </el-input>
            </div>

            <el-popover placement="bottom" width="200" trigger="click">
              <p class="font-weight-bold">Sort By</p>
              <div class="b-divider my-2"></div>
              <div class="w-100">
                <el-radio
                  v-model="filters.order_by"
                  v-for="(sort, n) in sortList"
                  :key="sort.name"
                  :class="n !== sortList.length - 1 && 'mb-3'"
                  :label="sort.value"
                >
                  {{ sort.name }}
                </el-radio>
              </div>
              <el-button
                v-ripple.400="'rgba(0,0,0,0.1)'"
                class="flat no-width"
                slot="reference"
              >
                <i class="bx bx-sort mr-1"></i> Sort
              </el-button>
            </el-popover>
          </div>
        </div>
        <div class="w-100 el-main-table" v-loading="loading">
          <div class="el-table-header" v-if="selected.length > 0">
            <div class="d-flex align-items-center">
              <el-checkbox
                :indeterminate="selected.length == orders.results.length"
                v-model="allCheck"
                @change="$refs.multipleTable.toggleAllSelection"
                >{{ selected.length }} selected</el-checkbox
              >
              <el-dropdown @command="updateStatus" trigger="click">
                <p
                  class="el-dropdown-link ml-3 pointer primary hover-underline"
                >
                  Actions
                  <i class="el-icon-arrow-down el-icon--right"></i>
                </p>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    v-for="action in actions"
                    :key="action.value"
                    :command="action.value"
                  >
                    {{ action.label }}</el-dropdown-item
                  >
                  <el-dropdown-item disabled> No Action</el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </div>
          </div>
          <el-table
            ref="multipleTable"
            :data="orders.results"
            style="width: 100%"
            @row-click="(row) => editCode(row)"
          >
            <el-table-column width="50" label="ID">
              <template slot-scope="scope">
                {{ scope.row.id }}
              </template>
            </el-table-column>
            <el-table-column label="Code" width="250">
              <template slot-scope="scope">
                {{ scope.row.code }}
              </template>
            </el-table-column>
            <el-table-column label="Status" width="250">
              <template slot-scope="scope">
                <el-tag
                  type="success"
                  class="rounded"
                  v-if="scope.row.status == 'Received'"
                  ><i class="bx bx-check-circle"></i>
                  {{ scope.row.status }}</el-tag
                >

                <el-tag
                  type="danger"
                  class="rounded"
                  v-if="scope.row.status == 'Cancelled'"
                  ><i class="bx bx-time-five"></i>{{ scope.row.status }}</el-tag
                >

                <el-tag
                  type="warning"
                  class="rounded"
                  v-if="scope.row.status == 'Pending'"
                  ><i class="bx bx-time-five"></i>{{ scope.row.status }}</el-tag
                >
              </template>
            </el-table-column>
            <el-table-column label="App Name" width="250">
              <template slot-scope="scope">
                {{ scope.row.app }}
              </template>
            </el-table-column>

            <el-table-column label="Date">
              <template slot-scope="scope">
                {{ $moment(scope.row.created_at).format("DD/MM/YYYY") }}
              </template>
            </el-table-column>

            <template #empty>
              <el-empty description="No data found!">
                <template #image> <empty /> </template
              ></el-empty>
            </template>
          </el-table>
          <div class="el-table-footer" v-if="orders.total_pages !== 1">
            <el-pagination
              background
              @current-change="
                (index) => ((orders.current_page = index), getOrders())
              "
              :current-page="orders.current_page"
              layout="prev, pager, next"
              :page-count="orders.total_pages"
            >
            </el-pagination>
          </div>
        </div>
      </div>
    </div>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openFulfillDialog"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Create Code</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <el-input v-model="code.code" placeholder="Code" class="w-100 mb-3">
        </el-input>

        <el-select v-model="code.app" class="w-100 mb-3">
          <el-option label="Pin Dou Dou" value="Pin Dou Dou"> </el-option>
          <el-option label="Tao Bao" value="Tao Bao"> </el-option>
          <el-option label="1688" value="1688"> </el-option>
        </el-select>

        <el-select v-model="code.status" class="w-100 mb-3">
          <el-option label="Received" value="Received"> </el-option>
          <el-option label="Pending" value="Pending"> </el-option>
          <el-option label="Cancelled" value="Cancelled"> </el-option>
        </el-select>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="openFulfillDialog = false"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400="'rgba(var(--primary),0.2)'"
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            :loading="creatingCode"
            @click="createNewCode"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="editCodeDialog"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Edit Code</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <el-input
          v-model="editingCode.code"
          placeholder="Code"
          class="w-100 mb-3"
        >
        </el-input>

        <el-select v-model="editingCode.app" class="w-100 mb-3">
          <el-option label="Pin Dou Dou" value="Pin Dou Dou"> </el-option>
          <el-option label="Tao Bao" value="Tao Bao"> </el-option>
          <el-option label="1688" value="1688"> </el-option>
        </el-select>

        <el-select v-model="editingCode.status" class="w-100 mb-3">
          <el-option label="Received" value="Received"> </el-option>
          <el-option label="Pending" value="Pending"> </el-option>
          <el-option label="Cancelled" value="Cancelled"> </el-option>
        </el-select>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="editCodeDialog = false"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400="'rgba(var(--primary),0.2)'"
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            :loading="updatingCode"
            @click="updateCode"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { empty } from "../../common";
export default {
  data() {
    return {
      filters: {
        query: "",
        order_by: "-updated_at",
        pageSize: 10,
        status: "",
        supplier: "",
        date: "",
        is_fulfilled: false,
      },
      openFulfillDialog: false,
      editCodeDialog: false,
      creatingCode: false,
      updatingCode: false,
      loading: false,
      code: {
        code: "",
        app: "Pin Dou Dou",
        status: "Pending",
      },
      editingCode: {},
      actions: [],
      sortList: [
        {
          name: "Created (oldest first)",
          value: "created_at",
        },
        {
          name: "Created (newest first)",
          value: "-created_at",
        },
        {
          name: "Updated (oldest first)",
          value: "updated_at",
        },
        {
          name: "Updated (newest first)",
          value: "-updated_at",
        },
        {
          name: "Supplier A–Z",
          value: "supplier__name",
        },
        {
          name: "Supplier Z–A",
          value: "-supplier__name",
        },
      ],
      suppliers: [],
      selectedDate: null,
      allCheck: false,
      gettingOrder: false,
      page: 1,
      max: 5,
      selected: [],
      orders: {
        current_page: 1,
        results: [],
        total_pages: 0,
      },
    };
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
  components: {
    empty,
  },
  created() {
    this.$store.commit("setIsReady", false);
    this.getOrders();
  },
  watch: {
    "orders.current_page": {
      handler: function () {
        this.getOrders();
      },
      deep: true,
    },
    filters: {
      handler: function () {
        this.getOrders();
      },
      deep: true,
    },
  },
  methods: {
    handleSelectionChange(val) {
      this.selected = val;
    },
    printPdf() {
      var tableRows = "";
      this.orders.results.forEach((order, index) => {
        tableRows += `<tr>
            <td>${index + 1}</td>
            <td>${order.code}</td>
            <td>${order.status == "Received" ? "Yes" : "No"}</td>
             <td>${this.$moment(order.created_at).format("DD/MM/YYYY")}</td>
        </tr>`;
      });
      var html = `
<!DOCTYPE html>
<html lang="en">
  <head>
    <meta charset="UTF-8" />
    <meta http-equiv="X-UA-Compatible" content="IE=edge" />
    <meta name="viewport" content="width=device-width, initial-scale=1.0" />
    <title>Order Codes</title>
    <style>
      table {
        font-family: arial, sans-serif;
        border-collapse: collapse;
        width: 100%;
        margin-bottom: 15px;
      }

      td,
      th {
        border: 1px solid #dddddd;
        text-align: left;
        padding: 8px;
      }
    </style>
  </head>
  <body>
    <table>
      <thead>
        <tr>
          <th style="width:50px;">No.</th>
          <th>Code</th>
          <th>Received</th>
          <th>Date</th>
        </tr>
      </thead>
      <tbody id="table-body">${tableRows}</tbody>
    </table>
  </body>
</html>
`;
      var newWin = window.open("", "Print-Window");
      newWin.document.open();
      newWin.document.write(html);
      newWin.window.print();
    },
    editCode(code) {
      this.editingCode = code;
      this.editCodeDialog = true;
    },
    updateCode() {
      this.updatingCode = true;
      this.$axios
        .put("order/", { data: this.editingCode })
        .then(() => {
          this.updatingCode = false;
          this.editingCode = {
            code: "",
            app: "Pin Dou Dou",
            status: "Pending",
          };
          this.editCodeDialog = false;
          this.getOrders();
          this.$noti.display({
            message: "Successfully updated!",
            status: "success",
            color: "success",
          });
        })
        .catch((err) => {
          this.editCodeDialog = false;
          this.updatingCode = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    createNewCode() {
      this.creatingCode = true;
      this.$axios
        .post("order/", { data: this.code })
        .then(() => {
          this.code = {
            code: "",
            app: "Pin Dou Dou",
            status: "Pending",
          };
          this.creatingCode = false;
          this.openFulfillDialog = false;
          this.getOrders();
          this.$noti.display({
            message: "Successfully created!",
            status: "success",
            color: "success",
          });
        })
        .catch((err) => {
          this.creatingCode = false;
          this.openFulfillDialog = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    getOrders() {
      this.loading = true;
      this.gettingOrder = true;
      if (this.filters.date === null) {
        this.filters.date = "";
      }
      var url = `order/list/?page=${this.orders.current_page}&page_size=${this.filters.pageSize}&query=${this.filters.query}&status=${this.filters.status}&order_by=${this.filters.order_by}`;
      this.$axios
        .get(url)
        .then((response) => {
          this.loading = false;
          this.gettingOrder = false;
          this.orders.results = response.data.results;
          this.orders.total_pages = response.data.total_pages;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.loading = false;
          this.gettingOrder = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
  },
};
</script>

